.projectsContainer {
  overflow-y: scroll;
  margin-right: -100px;
  padding-right: 100px;
  height: 100vh; }
  .projectsContainer .title {
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    margin-top: 10vh; }
  .projectsContainer .content {
    width: 100vw;
    /*    div:hover div img {
      left: 25px;
    }*/ }
    .projectsContainer .content div {
      width: 1000px;
      height: 300px;
      background-color: #f1f1f1;
      margin: 30px auto;
      display: -ms-flexbox;
      display: flex; }
      .projectsContainer .content div div {
        width: 500px;
        height: 300px;
        background-color: #f1f1f1;
        margin: 0;
        position: relative; }
        .projectsContainer .content div div img {
          position: absolute;
          left: 0;
          top: 22px;
          -webkit-transition: all .8s;
          -o-transition: all .8s;
          transition: all .8s; }
      .projectsContainer .content div article {
        width: 500px;
        height: 300px; }
        .projectsContainer .content div article h2 {
          text-align: center; }



